import React, {Component} from 'react';
import {Grid, Cell} from "react-mdl";

class Skills extends Component{
    render() {
        return(
            <Grid>
                <Cell col={8}>
                    <p><b>{this.props.title}: </b>{this.props.description}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Skills;
